import React, { useEffect } from 'react';
import { useNavigate } from 'react-router-dom';

const Logout = () => {
    const navigate = useNavigate();

    useEffect(() => {
        // Remova os tokens do localStorage
        localStorage.removeItem('accessToken');
        localStorage.removeItem('refreshToken'); // Se voc estiver armazenando o refresh token tambm

        // Redirecione o usurio para a pgina de login ou home
        navigate('/'); // Altere '/login' para a rota que voc deseja redirecionar aps o logout
    }, [navigate]);

    return null;
};

export default Logout;