import * as React from 'react';             // Importa a biblioteca React
import Button from '@mui/material/Button';  // Importa o componente Button do Material-UI
import Stack from '@mui/material/Stack';    // Importa o componente Stack do Material-UI


/**
 * CustomButton component.
 * 
 * @param {Object} props - Component properties.
 * @param {string} props.text - Texto a ser exibido no boto.
 * @param {React.ElementType} [props.icon_component] - Componente de cone a ser exibido no boto.
 * @param {string} [props.bgcolor] - Cor de fundo do boto.
 * @param {string} [props.text_color] - Cor do texto do boto.
 * @param {string} [props.font_family] - Fonte do texto do boto.
 * @param {string} [props.font_weight] - Peso da fonte do texto do boto.
 * @param {Function} [props.callback] - Funo a ser chamada quando o boto  clicado.
 * @returns {JSX.Element} The rendered button component.
 */

interface CustomButtonProps {
    text: string;
    icon_component?: React.ReactElement;
    bgcolor: string;
    text_color: string;
    font_family: string;
    font_weight?: string;
    callback?: () => void;
  }

function CustomButton({text, icon_component, bgcolor, text_color, font_family, font_weight, callback}: CustomButtonProps) {
  return (
    <Stack direction="row" spacing={2}>   {/* Usa Stack para alinhar os elementos em uma linha com espaamento entre eles */}
      <Button 
        variant="contained"               // Define o tipo do boto como 'contained'
        endIcon={icon_component}          // Adiciona o cone no final do boto, se fornecido
        onClick={callback}                // Define a funo a ser chamada quando o boto  clicado
        style={{
          backgroundColor: bgcolor,       // Define a cor de fundo do boto
          color: text_color,              // Define a cor do texto do boto
          fontFamily: font_family,        // Define a fonte do texto do boto
          fontWeight: font_weight,        // Define o peso da fonte do texto do boto
          textTransform: 'none'           // Garante que o texto no ser transformado (por exemplo, no ficar todo em maisculas)
        }}>
        {text}                            {/* Renderiza o texto do boto */}
      </Button>
      
    </Stack>
  );
}
export default CustomButton